.home-page(){
  &{width: 100%;height: 100%;}
  .top-logo-box{display: none;}
  .menu-box{
    width: 100%;
    height: calc(100% - 66.6666vw);
    .menu-list{
      height: 100%;
      .menu-item{
        overflow: hidden;
        float: left;
        position: relative;
        &>a{
          width: 100%;
          height: 100%;
          display: flex;
          flex-direction: column;
          flex-wrap: nowrap;
          justify-content: center;
          align-items: center;
          align-content: center;
        }
        span{
          text-align: center;
          width: 100%;
          display: block;
          color: #000;
        }
        .menu-icon{margin:0 auto;}
        .title{
          font-size: @font-size-h3;
        }
        .desc{
          font-size: @font-size-h4;
        }
        &:nth-child(-n+2){
          width: 50%;
          height: 60%;
          border-top: 1px solid @color-line-border;
          border-bottom: 1px solid @color-line-border;
          .title{ font-size: @font-size-h2; }
          .desc{font-size: @font-size-h4;padding-bottom: 5px}
        }
        &:nth-child(n+3){
          width: 33.3333%;
          height: 40%;
          .menu-icon{order: 1;}
          .title{ order: 2;padding-top: 5px}
          .desc{display: none;}
          border-right: 1px solid @color-line-border;
        }
        &:nth-child(1){border-right: 1px solid @color-line-border; .title{color: @color-theme-A}; .menu-icon{.icon-home-menu-hssy();}}
        &:nth-child(2){.title{color: @color-theme-A}; .menu-icon{ .icon-home-menu-hqdz();}}
        &:nth-child(3){.menu-icon{.icon-home-menu-jslp();}}
        &:nth-child(4){.menu-icon{.icon-home-menu-hyyd();}}
        &:nth-child(5){border-right: none; .menu-icon{.icon-home-menu-hjzs();}}
      }
      .clearfix();
    }
  }
}

