.hssy-suit-list-page(){
  .list-box{
    .list-hive-01(@list_n: 1;@aspectRatio:(2/3);@info-box-height: (30 * @rem));
    .info-box{
      span{line-height: (30 * @rem);font-family: @font-family-serif;}
      .title{float: left;font-size: @font-size-h2;color: @color-theme-B;}
      .unit{float: right;color: @color-theme-A;font-size: @font-size-h3;}
      .price{float: right;font-size: @font-size-h1;color: @color-theme-A;margin-right: 4px;position: relative;top: (-3 * @rem)}
    }
  }
}