.iconSize(@width;@height;@bgSize:100%;){
  width: unit(@width,px);
  height: unit(@height,px);
  background-size: @bgSize;
  display: block;
  background-repeat: no-repeat;
}
///全站一级菜单图标
.icon-first-menu(){
  background-image: url("../images/icon-first-menu.png");
  .iconSize(20;20;100px);
}

///箭头图标
.icon-open-button(){background-image: url("../images/icon-open-button.png");.iconSize(20;15;200%);}
.icon-open-button-fold(){background-position-x:-20px;}
.icon-open-button-unfold(){background-position-x:0px;}
