
.sample-list-page(){
  .sift-nav{
    .sift-nav-box{
      @item-height:20px;

      width: 100%;
      height: 42px;
      padding-left: 60px;
      padding-right: 60px;
      background-color: #fff;
      border-top:1px solid @color-line-border;
      border-bottom:1px solid @color-line-border;
      position: relative;
      text-align: center;
      span{font-size:@font-size-h4-px;line-height: @item-height;display: block}
      .sift-title{
        position: absolute;
        top: @item-space;
        left: @item-space;
        background-color: fadeout(#e73b63,20%);
        border-radius: @item-space;
        width:(60px - (@item-space * 2));
        height: @item-height;
        line-height: @item-height;
        .text{font-size: @font-size-h4-px;}
      }
      .sift-list-box{
        width: 100%;
        height: 100%;
        overflow: hidden;
        margin: auto;
        ul{
          width: 1000%;
          padding-top: @item-space;
          padding-bottom: @item-space;
          li{
            float: left;
            height: @item-height;
            margin-right: 10px;
            font-size: @font-size-h4-px;
          }
        }
      }
      .sift-button{
        position: absolute;
        top: @item-space;
        right: @item-space;
        width:(60px - @item-space);
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        .icon{.icon-sift-button();}
      }
    }
  }

  .list-box{
    .list-hive-01();
  }
}

