//一级主菜单
.first-menu(){
  display: flex;
  position: fixed;
  z-index: @zindex-tooltip;
  bottom: 0;
  width: 100%;
  height: @first-menu-height;
  flex-direction: row;
  background-color: #fff;
  border-top: 1px solid #d6d6d6;
  padding-top: 6px;
  .item{
    flex-grow: 1;
    width: 25%;
    text-align: center;
    .icon{
      display: block;
      margin: 0 auto;
      width: 30px;
      height: 30px;
      .icon-first-menu();
    }
    .title{font-size: 12px;line-height: 18px;display: block;width: 100%;height: 20px;padding-top: 2px}
    &:nth-child(1) .icon{.icon-first-menu-1();}
    &:nth-child(2) .icon{.icon-first-menu-2();}
    &:nth-child(3) .icon{.icon-first-menu-3();}
    &:nth-child(4) .icon{.icon-first-menu-4();}
    &:nth-child(5) .icon{.icon-first-menu-5();}
    &.item-activate{
      .icon{.icon-first-menu-activate();}
      .title{color: @color-theme-A}
    }
  }
}
//二级主菜单
.secondly-menu(){
  position: fixed;
  z-index: @zindex-popover;
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  top: 0;
  left: -68px;
  //z-index: @zindex-popover;
  .transition-duration(0.2s);
  .menu-box{
    position: relative;
    box-sizing: content-box;
    padding: 3px 3px 3px 0;
    border-bottom-right-radius: 5px;
    background-color: #ffdb08;
    .item{
      width: 65px;
      height: 35px;
      text-align: center;
      &:last-child{margin-bottom: 0;}
      &:nth-child( 5n + 1){background-color: #10437a}
      &:nth-child( 5n + 2){background-color: #ec242e}
      &:nth-child( 5n + 3){background-color: #fd720d}
      &:nth-child( 5n + 4){background-color: #e73b63}
      &:nth-child( 5n + 5){background-color: #0b9a64}
      .title{font-size: 12px ;color: #fff;line-height: 35px;display: block;}
      &.item-activate .title{color: #fbd213;}
    }
    .open-button{
      position: absolute;
      top: 0;
      right: -50px;
      width: 50px;
      height: 73px;
      .info-box {
        width: 35px;
        height: 100%;
        position: relative;
        text-align: center;
        .title{
          display: block;
          position: absolute;
          width: 30px;
          height: ( @font-size-h4-px * 2);
          font-size: @font-size-h4-px;
          line-height: @font-size-h4-px;
          top:25%;
          left: 50%;
          color: #fff;
          z-index: 100;
          margin-top: (-1 * @font-size-h4-px + 2);
          margin-left: -15px;;
        }
        .icon{
          position: absolute;
          z-index: 100;
          top:70%;
          left: 50%;
          margin-top: -9px;
          margin-left: -12px;
          zoom: 0.8;
          .icon-open-button();
          .icon-open-button-unfold();
        }
        .bg-box{
          border-top-right-radius: 5px;
          position: relative;
          height: calc(100% - 25px);
          background-color: #ffdb08;
          padding:3px;
          &:before{
            content: "";
            display: block;
            background-color: #000;
            width: 100%;
            height: 35px;
            margin:auto;
            border-radius: 2px;
          }
          &:after{
            content: "";
            display: block;
            position: absolute;
            bottom: -25px;
            left: 0;
            border-style: solid;
            border-color: transparent transparent transparent #ffdb08;
            border-width: 0 0 25px 35px;
            width: calc(100% - 6px);
            margin:auto;
          }
        }
      }

    }
  }
  &.fold{
    .menu-box .open-button .icon{.icon-open-button-fold();}
    left: 0;
    .transition-duration(0.2s);
  }
}

//logo标题
.top-logo-box(){
  height: 44px;
  width: 100%;
  background-color: @color-theme-A;
  .logo-box{
    height: 44px;
    width: 300px;
    margin: 0 auto;
  }
  .icon-home-logo{
    float: left;
    .icon-home-logo();
    margin-top: 12px;
  }
  .icon-home-word{
    float: right;
    margin-top: 13px;
    .icon-home-word();
  }
}

//顶部图片广告
.adv-header-box(){
  position: relative;
  width: 100%;
  .heightVW(66.6666);
  .carousel-picture() {
    width: 100%;
    height: 100%;
    overflow: hidden;
    ul {
      flex-direction: row;
      width: 100%;
      li {
        float: left;
        width: 100vw;
      }
      .clearfix();
    }
    .point-box{
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      display: flex;
      margin-bottom: 5px;
      .point{
        background-color: #fff;
        width: 10px;
        height: 10px;
        border-radius: 5px;
        margin-left: 3px;
        margin-right: 3px;
        &.point-activate{
          background-color: @color-theme-A;
        }
      }
    }
  }
  .carousel-picture{
    .carousel-picture();
  }
}

///菜单
.title-box-style-1{
  text-align: center;
  position: relative;
  padding-top: 5px;
  .banner {
    position: relative;
    text-align: center;
    margin-top: 5px;
    margin-bottom: 5px;
    i {
      display: block;
      width: 100%;
      height: 1px;
      background-color: @color-line-border;
      position: absolute;
      top: 50%;
      z-index: 0;
    }
    span {
      position: relative;
      z-index: 1;
      color: #fff;
      background-color: @color-theme-A;
      border-radius: 4px;
      font-size: @font-size-h4;
      padding: 3px 7px;
    }
  }
  .title{
    font-size: @font-size-h2;
    font-weight: bold;
  }
  &:before{
    height: (24 * @rem);
    content:" ";
    .icon-block-tit-box-1-1();
    display: block;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 60%;
  }
  &:after{
    content: " ";
    .icon-block-tit-box-1-2();
    display: block;
    background-position: center center;
    background-repeat: no-repeat;
    height: (18 * @rem);
    background-size: 80%;
  }
}

///图片
.list-photo-box{
  ul{
    font-size: 0;
    li{
      text-align: center;
      img{width: auto;max-width: 100%}
    }
  }
  &.photo-space ul li{margin-bottom: @item-space;}
}

.fixed-center-box(){position: fixed;top:0;left:0;z-index:@zindex-popover;width: 100%;height: 100%;display: flex;align-items: center}
.fixed-center-box{
  .fixed-center-box();
};

.sift-content-box{
  &{
    left: 100%;
    justify-content: flex-end;
    flex-wrap: wrap;
    .transition-duration(1s);
  }
  &.show-sift{
    left: 0;
  }
  .sift-center-box{
    max-width: 80%;
    min-width: 40%;
    border:1px solid @color-line-border;
    background-color: #fff;
    .sift-scrollView-box{
      overflow: auto;
      max-height:calc(100vh - 150px);
    }
    .content-box{
      width: 100%;
      padding-left: @item-space;
      padding-right: @item-space;
      .title{
        width: 100%;
        height: 40px;
        display: flex;
        justify-content: flex-start;
        flex-direction: row;
        align-items: center;
        .text{line-height: 40px;font-size: @font-size-h3-px;display: block;margin-left: 5px}
      }
      .item-list{
        width: calc(100% + 10px);
        margin-right: -10px;
        .clearfix();
        text-align: center;
        .item{
          float: left;
          border-radius: 5px;
          height: 30px;
          min-width: 60px;
          padding-left:5px;
          padding-right: 5px;
          margin-bottom: 10px;
          margin-right: 10px;
          border: 1px solid @color-line-border;
          .title{font-size: @font-size-h4-px;line-height: 30px;display: block}
          &.item-activate{
            .title{color: @color-theme-A;}
            border-color: @color-theme-A;
          }
        }
      }
      &.type .title .icon{.icon-pringles-sift();.icon-pringles-sift-1();}
      &.style .title .icon{.icon-pringles-sift();.icon-pringles-sift-2();}
      &.address .title .icon{.icon-pringles-sift();.icon-pringles-sift-3();}
    }
    .button-box{
      display: flex;
      height: 35px;
      flex-direction: row;
      border-top: 1px solid @color-line-border;
      text-align: center;
      .confirm-button,.reset-button{flex: 1;font-size: @font-size-h3-px;line-height: 35px;}
      .confirm-button{background-color: @color-theme-A;}
    }
  }
}
