.hssy-home-page(){
  &{width: 100%;}

  .list-sample{.list-hive-01();}
  .list-pringles{.list-hive-01();}
  .list-suit{
    .list-hive-01(@list_n:1;@space: @item-space;@aspectRatio: 0.25);
    .suit-box{
      margin-bottom: 20px;
      overflow: hidden;
      display: flex;
      flex-direction: row;
      .photo-box{
        overflow: hidden;
        width: 50%;
        height: 100%;
        img{width: 100%;}
      }
      .info-box{
        width: 50%;
        height: 100%;
        text-align: left;
        padding-left: @item-space;
        position: relative;
        overflow: auto;
        .img-title{
          .icon-suit-title-img();
          background-repeat: no-repeat;
          background-position: center left;
          background-size: 50%;
          display: block;
          width: 100%;
          height: @font-size-h1;
        }
        .text-title{
          display: block;width: 100%;font-size: @font-size-h3;line-height:@font-size-h3;margin-bottom:2px;;font-weight: bold;
          height: @font-size-h3;
          &:active{color: @color-theme-A;}
        }
        .text-content{
          display: block;
          padding-top: 3px;
          font-size: @font-size-h4;
          line-height:@font-size-h4;
          font-weight:normal;
          color: #949191;
        }
      }
    }
  }
}


