//筛选器

.filter-1-jsbn-hive(@ad_w:@default_w){
  @top:14;
  @tab_h:18;
  @font_s:12;
  .paddingTop(@top;@ad_w);
  .line-bot-hive(0;10;@ad_w);
  .clear();
  border-color:#eee;
  span{
    color: #666;cursor: pointer;    
    .padding(@ad_w,1,3);
    .fontSize(@font_s;@ad_w); 
    .height(@tab_h;@ad_w);
    .lineHeight(@tab_h;@ad_w);
  }
  .l-box{
    float:left;
    .height(@tab_h;@ad_w);
    .lineHeight(@tab_h;@ad_w);
    .width(60;@ad_w);
  }
  .title{
      display:inline-block;float:left;position:relative;
      .width(75;@ad_w);
      .paddingLeft(26;@ad_w);
      .height(@tab_h;@ad_w);
      .lineHeight(@tab_h;@ad_w);
     b{
      color:#333;
      .fontSize(14;@ad_w); 
    }
     i{
      display:inline-block;vertical-align:middle;position:absolute;left:0;
      .bottom(0;@ad_w);
     }
  }
  .tab-box{
    display:block;float:left;
    .width(1090;@ad_w);
    .clear();
  }
  .r-box{
    float:left;
    .width(1030;@ad_w);
    .clear();
    li{
      float:left;
     .width(110,@ad_w);
     .height(@tab_h;@ad_w);
     .lineHeight(@tab_h;@ad_w);
     .marginBottom(7;@ad_w);      
    }
  }
  .tab-sel{background-color: #ffa8bf;color: #fff;}
  .tab{
    display:inline-block;
    .transitions-all(0.2s);
    &:hover{
      color: #000;
      background: #eee;
    }
  }   
}

//缩略图

.thumbnail-1-hive(
  @w:1200;
  @big_h:0;
  @big_mgb:0;
  @hover_box_w:@w*0.2;
  @bg_w:0;
  @ico_w:0;
  @ico_h:0;
  @item_num:0;
  @border:0;
  @border_c:#fff;
  @border_c2:#FC5682;
  @ad_w:@default_w){  
  @hover_box_w:@w*0.2;
  @item_w:@w/@item_num - @border*2;
  @item_h:@item_w*2/3;
  .width(@w;@ad_w);
  .big-img-box{
    width:100%;position: relative;text-align: center;overflow:hidden;
    .height(@big_h;@ad_w);
    .marginBottom(@big_mgb;@ad_w);
    img{height:100%;}
    i{
      position:absolute;top:50%;left:50%;
      .marginTop(-@ico_h/2;@ad_w);
      .marginLeft(-@ico_w/2;@ad_w);
      .opacity(60);
    }
  }
  .bg-ico{
    position:absolute;top:50%;left:50%;background-color: #fff;
    .height(@bg_w*2;@ad_w);
    .width(@bg_w;@ad_w);
    .marginTop(-@bg_w;@ad_w);
    .marginLeft(-@bg_w/2;@ad_w);
    .opacity(20);
  }

  .left-hover-box{
    height:100%;position:absolute;top:0;left:0;cursor:pointer;
    .width(@hover_box_w;@ad_w);
    &:hover{
      i{.opacity(100);}
      .bg-ico{.opacity(50);}
    }
  }
  .right-hover-box{
    height:100%;position:absolute;top:0;right:0;cursor:pointer;
    .width(@hover_box_w;@ad_w);
    &:hover{
      i{.opacity(100);}
      .bg-ico{.opacity(50);}
    }    
  }
  
  

  .small-img-box{
    .clear();
  }
  .item{
    border:@border solid @border_c;text-align: center;float:left;overflow:hidden;background-color:#111;cursor: pointer;
    .width(@item_w;@ad_w);
    .height(@item_h;@ad_w);
    img{height:100%;}
  }
  .item-current{border:@border solid @border_c2;}
} 


//幻灯切换

.switch-1-hive(){
  position:absolute;top: 0;width:100%;height:100%;text-align: center;
  .hover-box{position:absolute; height:100%;width:15%;top:0; cursor:pointer;opacity: 0;filter:Alpha(opacity=0);}
  .hover-box{position:absolute; height:100%;width:15%;top:0; cursor:pointer;opacity: 0;filter:Alpha(opacity=0);}
  .arrow{position:absolute;top:50%;width:12px;height:19px;margin-top: -10px;}
  .arrow-bg{position:absolute;top:50%;display:inline-block;width:28px;height:28px;margin-top: -15px; background-color: #fff;-moz-border-radius: 14px;-webkit-border-radius: 14px;border-radius: 14px; }
  .l-hover{left:0;}
  .r-hover{right:0;}
  .l-hover .arrow{left:50%;margin-left: -6px;background:url(../img/arrow-small-js.png) no-repeat;}
  .r-hover .arrow{right:50%;margin-right: -6px;background:url(../img/arrow-small-js.png) no-repeat;background-position:0 -19px;}
  .l-hover .arrow-bg{left:50%;margin-left: -14px;}
  .r-hover .arrow-bg{right:50%;margin-right: -14px;}
  .dot-box{display:inline-block;margin-top: 300px;opacity: 0.6;filter:Alpha(opacity=60);}
  .dot-box li{padding:2px 5px;display:inline-block;cursor:pointer;}
  .dot-box i{background-color: #ddd;display:inline-block; width:6px;height:6px;-moz-border-radius: 5px;-webkit-border-radius: 5px;border-radius: 5px;border:2px solid #333;transition: all 0.3s;}
  .dot-box .sel i{background-color: #fff;-moz-border-radius: 6px;-webkit-border-radius: 6px;border-radius: 6px;border:3px solid #333;}
  &:hover{.arrow-bg{opacity: 0.6;filter:Alpha(opacity=60);}}
  &:hover .hover-box,.hover-box:hover .arrow-bg,&:hover .dot-box{opacity: 0.8;filter:Alpha(opacity=80);}
  
}





//css图标-三角
.arrow-1-hive(@_,@color:#fff,@arrow_w:0,@arrow_h:0,@ad_w:@default_w){display:block;width:0;height:0;border-style:solid;display:block;}
.arrow-1-hive(l,@color:#fff,@arrow_w:0,@arrow_h:0,@ad_w:@default_w){
  @w:unit(@arrow_w,@unit)*@ad_w/@default_w/2;
  @h:unit(@arrow_h,@unit)*@ad_w/@default_w;
  border-color:transparent @color transparent transparent;border-width:@w @h @w 0;
}
.arrow-1-hive(r,@color:#fff,@arrow_w:0,@arrow_h:0,@ad_w:@default_w){
  @w:unit(@arrow_w,@unit)*@ad_w/@default_w/2;
  @h:unit(@arrow_h,@unit)*@ad_w/@default_w;
  border-color:transparent transparent transparent @color;border-width:@w 0 @w @h;
}
.arrow-1-hive(b,@color:#fff,@arrow_w:0,@arrow_h:0,@ad_w:@default_w){
  @w:unit(@arrow_w,@unit)*@ad_w/@default_w/2;
  @h:unit(@arrow_h,@unit)*@ad_w/@default_w;
  border-color:@color transparent transparent;border-width:@h @w 0;
}
.arrow-1-hive(t,@color:#fff,@arrow_w:0,@arrow_h:0,@ad_w:@default_w){
  @w:unit(@arrow_w,@unit)*@ad_w/@default_w/2;
  @h:unit(@arrow_h,@unit)*@ad_w/@default_w;
  border-color:transparent transparent @color;border-width:0 @w @h;
}



/* uikit关闭按钮 */
.uk-close:after{
  display:none;
}
.uk-close-alt{
  height:36px;
  width:36px;
  padding:0;
  border-radius:0;
  box-shadow:none;
  background:url(../img/fancybox_sprite.png) 0 0 no-repeat;
}

/* uikit左右按钮 */
.uk-slidenav-previous{position: relative;}
.uk-slidenav-previous:before{
  position: absolute;top: 50%;left: 0;margin-top: -18px; display: block; content: "";width: 36px;height: 36px;
  background:url(../img/fancybox_sprite.png) 0 -36px no-repeat;
}
.uk-slidenav-next{position: relative;}
.uk-slidenav-next:before{position: absolute;top: 50%; right: 0;margin-top: -18px; display: block; content: "";width: 36px;height: 36px; background:url(../img/fancybox_sprite.png) 0 -72px no-repeat;}

