@font-size-px:            0.0625rem; // ~1px
@rem:            0.0625rem; // ~1px

@font-size-h1:            (@rem * 24);
@font-size-h2:            (@rem * 18);
@font-size-h3:            (@rem * 14);
@font-size-h4:            (@rem * 12);
@font-size-h1-px:            (24px);
@font-size-h2-px:            (18px);
@font-size-h3-px:            (14px);
@font-size-h4-px:            (12px);


@font-family-sans-serif:  "Helvetica Neue", Helvetica, Arial, sans-serif;
@font-family-serif:       Georgia, "Times New Roman", Times, serif;
@font-family-monospace:   Menlo, Monaco, Consolas, "Courier New", monospace;
@font-family-base:        @font-family-sans-serif;

@color-line-border: #d6d6d6;
@color-theme-A: #f94267;
@color-theme-B: #efa507;
@color-theme-default:#000;

@item-space:  10px;
@first-menu-height:  50px;

@zindex-navbar:            10000;
@zindex-dropdown:          10000;
@zindex-popover:           10060;
@zindex-tooltip:           10070;
@zindex-navbar-fixed:      10030;
@zindex-modal-background:  10040;
@zindex-modal:             10050;


//初始化
*{outline:none;
  //font-family:"微软雅黑";
  font-family: @font-family-base;
  box-sizing: border-box;
}
body,div,ul,dl,li,ol,h1,h2,h3,h4,h5,h6,dt,dd,img,a,span,p,table,td,i,input,button,textarea,form{padding:0;margin:0;border:none;}
ul,li,dl,dt,dd,ol{list-style:none;}
h1,h2,h3,h4{color:#333;}
a,span,p,font,b,i,strong{font-size:1rem;font-weight: normal;}
em{font-style:normal;}
i{font-style:normal;}
a{color:#333;text-decoration:none;}
a:hover{text-decoration:none;}
img{width: 100%}
html{width: 100%;height: 100%;}
body{width: 100%;height: 100%;}

@media (max-width: 320px) {
  html{font-size: 16px}
}
@media (min-width: 321px) and (max-width: 340px) {
  html{font-size: 17px}
}
@media (min-width: 341px) and (max-width: 360px) {
  html{font-size: 18px}
}
@media (min-width: 361px) and (max-width: 380px) {
  html{font-size: 19px}
}
@media (min-width: 381px) and (max-width: 400px) {
  html{font-size: 20px}
}
@media (min-width: 401px) and (max-width: 420px) {
  html{font-size: 21px}
}
@media (min-width: 421px) and (max-width: 440px) {
  html{font-size: 22px}
}
@media (min-width: 441px) and (max-width: 460px) {
  html{font-size: 23px}
}
@media (min-width: 461px) and (max-width: 480px) {
  html{font-size: 24px}
}
@media (min-width: 481px) and (max-width: 767px) {
  html{font-size: 28px}
}
@media (min-width: 768px) {
  html{font-size: 36px}
}

//@centi: 0.2; //约等于百分之一单位
//@unit: vw;
.heightVW(@h_s){
  .if(@s) when(isnumber(@s)){
    height:unit(@s,vw);
  }
  .if(@h_s);
}
//基础列表
.list-hive-01(@list_n:2;@space:@item-space;@aspectRatio:1.5;@info-box-height:0){
  margin-bottom: @item-space;
  ul{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding:  @item-space @item-space 0;
    li{
      @width: (100vw - @space * ( @list_n + 1))/@list_n;
      width: calc(@width);
      height: calc(@width * @aspectRatio + @info-box-height);
      overflow: hidden;
      &>a{display: block;width: 100%;height: 100%;}
      img{width: 100%;}
      .info-box{width: 100%;height: @info-box-height;}
      .photo-box{ width: 100%;height: calc(100% - @info-box-height);overflow: hidden;}
      margin-bottom: @space;
    }
    margin-bottom:(-1 * @space );
  }
}

.more-button{
  width: 100%;
  height: (30 * @rem);
  .button-box{
    padding-right: @item-space;
    padding-left: (10 * @rem);
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end;
    align-items: center;
    float: right;
    .icon{
      .icon-more-button();
      display: inline-block;
      width: @font-size-h3;
      height: @font-size-h3;
      background-size: 100%;
    }
    .title{
      display: inline-block;
      font-size: @font-size-h4;
      line-height: @font-size-h3;
    }
  }
}
.fixed_top_nav{ position:fixed;top:0;left:0;z-index:@zindex-tooltip; width:100%;}

